export default {
	data() {
		return {
			type: '1',
			code: '',
			orderId: 0,
			title: '提货'
		}
	},
	// type = 1 提货  2 到货
	onLoad(params) {
		switch (params.type) {
			case '1':
				uni.setNavigationBarTitle({
					title: '提货码'
				});
				this.title = '提货';
				break;
			case '2':
				uni.setNavigationBarTitle({
					title: '到货码'
				});
				this.title = '到货';
				break;
		}
		this.type = params.type;
		this.orderId = params.orderId;
	},
	methods: {
		submit() {
			let _this = this;
			if (_this.code.length < 6) {
				return uni.showToast({
					icon: "none",
					title: '请输入正确的6位码'
				})
			}
			let data = {};
			let url = '';
			switch (_this.type) {
				case '1':
					data.orderId = _this.orderId;
					data.driverId = uni.getStorageSync("userInfo").driverId;
					data.sendCode = _this.code;
					url = _this.http.driverPickup;
					break;
				case '2':
					data.orderId = _this.orderId;
					data.sendCode = _this.code;
					url = _this.http.userPickup;
					break;
			}
			_this.request({
				url: url,
				data: data,
				success: (res) => {
					if (res.code === '0') {
						uni.showToast({
							icon: "success",
							title: "操作成功"
						});
						setTimeout(() => {
							uni.redirectTo({
								url: '/pages/robSuccess/robSuccess?orderId=' + this.orderId
							});
						}, 1500)
					} else {
						uni.showToast({
							icon: "none",
							title: res.message
						})
					}
				}
			})
		},
		cancel() {
			uni.navigateBack();
		}
	}
}
